.group{width:100%;display:flex;flex-direction:column;justify-content:center;position:relative}.group .input{width:100%;padding:10px 25px 10px 15px;font-size:18px;background-color:var(--inputBackgroundColor);color:var(--mainColor);outline:none;border-radius:10px}.group .icon{position:absolute;right:15px;color:var(--subColor)}.pending .input{background-color:#dfdfdf}.error .input{border-color:red}.error .input::placeholder{color:red}.error .helperText{color:red}.switch{width:100%;display:flex;justify-content:space-between}.switch .placholder{color:var(--subColor)}.input-switch{position:relative;display:inline-block;width:50px;height:28px}.input-switch input{opacity:0;width:0;height:0;border:none !important}.input-switch .slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background-color:#ccc;transition:.4s;border-radius:34px}.input-switch .slider:before{position:absolute;content:"";height:22px;width:22px;left:3px;bottom:3px;background-color:#fff;transition:.4s;border-radius:50%}.input-switch input:checked+.slider{background-color:#000}.input-switch input:checked+.slider:before{transform:translateX(22px)}.input-switch .disabled{opacity:.6;pointer-events:none}